export default {
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			addressData: {
				address: '',
				city: '',
				id: '',
				name: '',
				phone: '',
				province: '',
				zone: '',
			},
			goodData: [],
			totalPrice: 0,
			orderData: {},
			allDeliveryFee: 0
		}
	},
	onLoad(option) {
		//商品数据
		let data = JSON.parse(option.data).orderTemp;
		this.goodData = data.goodsData;
		this.totalPrice = data.totalPrice;
		this.allDeliveryFee = data.allDeliveryFee;
		this.orderData = data;
		this.loadDefaultAddr();
		this.calPrice(data);
	},
	methods: {
		calPrice(data) {
			console.log(data);
		},
		submit() {
			if (!this.addressData.id) {
				this.$api.msg("请选择收货地址");
				return;
			}
			this.orderData["addressData"] = this.addressData;
			uni.redirectTo({
				url: `/pages/money/pay?data=${JSON.stringify({
					orderTemp: this.orderData
				})}`
			})
		},
		loadDefaultAddr() {
			var that = this;
			this.$http.get(this.$httpApi.address.defaultAddr, {}).
			then(function(response) {
				//这里只会在接口是成功状态返回
				that.addressData.address = response.address;
				that.addressData.city = response.city;
				that.addressData.id = response.id;
				that.addressData.name = response.name;
				that.addressData.phone = response.phone;
				that.addressData.province = response.province;
				that.addressData.zone = response.zone;
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
			});
		}
	}
}
